import React from 'react';

import '../style.scss';

import Menu from '../../Menu/index';
import Footer from '../Footer/index'

import Img1 from '../../../assets/campanhas/img/doritos/t1doritos.jpg';
import Img2 from '../../../assets/campanhas/img/doritos/t2doritos.jpg';

class Doritos extends React.Component {

  componentDidMount() {
    window.scrollTo(0, 0)
  }

  tipoMenu = 'campanhas';

  controlesAtivos = 'todos';
  links = {
    prev: '/havaianas',
    next: '/burger-king'
  }

  controlesFooter = {
    controlesAtivos: this.controlesAtivos,
    links: this.links
  }

  render() {
    return (
      <div className="campanha">
        <div className="campanha-container">
          <Menu tipoMenu={this.tipoMenu} />
          <div className="descricao-box">
            <div className="descricao-titulo">
              <div className="titulo-box">
                <h1 className="titulo-campanha">Trigono- metria</h1>
                <p className="cliente-agencia">Client: Elma Chips | Agency: AlmapBBDO</p>
                <p className="funcao">Role: Concept / Art Direction</p>
              </div>
            </div>
            <div className="descricao-texto">
              <div className="paragrafo-box">
                <p className="primeiro">
                  Most students, think trigonometry is a boring and a difficult<br />
                  theme to study about. And what is the result? A lot of students<br />
                  get lost over the questions about it during the most important<br />
                  national university exam in Brazil: ENEM.
              </p>
                <p className="segundo">
                  Lucky them, their favorite snack has the shape of a triangle.<br />
                  Actually, Doritos is the only Snack with a triangular shape<br />
                  in the world. So guess what? Doritos has found on itself the<br />
                  ultimate solution to teach them about trigonometry, in a<br />
                  delicious and bold way.
              </p>
                <p className="terceiro">
                  The dynamic is very simple, any person with a Doritos pack<br />
                  is free to use a hotsite where it's possible to scan random<br />
                  Doritos tortillas that generates random trigonometry<br />
                  questions. Each question answered right gets points<br />
                  to be exchanged for a free Doritos pack.
              </p>
              </div>
            </div>
          </div>
          <div className="imagens">
            <img src={Img1} className="img-doritos-1" alt="Doritos 1" />
            <img src={Img2} className="img-doritos-2" alt="Doritos 2" />
          </div>
          <Footer controlesFooter={this.controlesFooter} />
        </div>
      </div>
    );
  }
}

export default Doritos;
