import React from 'react';

import './style.scss';

import { Link } from 'react-router-dom';

class Controls extends React.Component {

  controlesAtivos = 'todos';
  links = {
    prev: '',
    next: ''
  };

  constructor(props) {
    super(props);
    this.controlesAtivos = props.controlesFooter.controlesAtivos;
    this.links = props.controlesFooter.links;
  }

  render() {

    if (this.controlesAtivos === "todos") {
      return (
        <div className="controls">
          <div className="prev">
            <Link to={this.links.prev}>&lt; prev</Link>
          </div>
          <div className="home">
            <Link to="/">home</Link>
          </div>
          <div className="next">
            <Link to={this.links.next}>next &gt;</Link>
          </div>
        </div>
      )
    } else if (this.controlesAtivos === "prev") {
      return (
        <div className="controls">
          <div className="prev">
            <Link to={this.links.prev}>&lt; prev</Link>
          </div>
          <div className="home">
            <Link to="/">home</Link>
          </div>
          <div className="next">
            {/* <Link to={this.links.next}>next &gt;</Link> */}
          </div>
        </div>
      )
    } else if (this.controlesAtivos === "next") {
      return (
        <div className="controls">
          <div className="prev">
            {/* <Link to={this.links.prev}>&lt; prev</Link> */}
          </div>
          <div className="home">
            <Link to="/">home</Link>
          </div>
          <div className="next">
            <Link to={this.links.next}>next &gt;</Link>
          </div>
        </div>
      )
    }
  }
}

export default Controls;
