import React from 'react';

import '../style.scss';

import Menu from '../../Menu/index';
import Footer from '../Footer/index'

import Img1 from '../../../assets/campanhas/img/whiskas/t1whiskas.jpg';
import Img2 from '../../../assets/campanhas/img/whiskas/t2whiskas.jpg';
import Img3 from '../../../assets/campanhas/img/whiskas/t3whiskas.jpg';
import Img4 from '../../../assets/campanhas/img/whiskas/t4whiskas.jpg';
import Img5 from '../../../assets/campanhas/img/whiskas/t5whiskas.jpg';
import Img6 from '../../../assets/campanhas/img/whiskas/t6whiskas.jpg';
import Img7 from '../../../assets/campanhas/img/whiskas/t7whiskas.jpg';
import Img8 from '../../../assets/campanhas/img/whiskas/t8whiskas.jpg';
import Img9 from '../../../assets/campanhas/img/whiskas/t9whiskas.jpg';
import Img10 from '../../../assets/campanhas/img/whiskas/t10whiskas.jpg';
import Img11 from '../../../assets/campanhas/img/whiskas/t11whiskas.jpg';
import Img12 from '../../../assets/campanhas/img/whiskas/t12whiskas.jpg';
import Img13 from '../../../assets/campanhas/img/whiskas/t13whiskas.jpg';
import Img14 from '../../../assets/campanhas/img/whiskas/t14whiskas.jpg';

class Whiskas extends React.Component {

  componentDidMount() {
    window.scrollTo(0, 0)
  }

  tipoMenu = 'campanhas';

  controlesAtivos = 'next';
  links = {
    prev: '',
    next: '/boticario'
  }

  controlesFooter = {
    controlesAtivos: this.controlesAtivos,
    links: this.links
  }

  render() {
    return (
      <div className="campanha">
        <div className="campanha-container">
          <Menu tipoMenu={this.tipoMenu} />
          <div className="descricao-box">
            <div className="descricao-titulo">
              <div className="titulo-box">
                <h1 className="titulo-campanha">Curiosity Box</h1>
                <p className="cliente-agencia">Client: Mars pet - Whiskas | Agency: AlmapBBDO</p>
                <p className="funcao">Role: Concept / Art Direction</p>
              </div>
            </div>
            <div className="descricao-texto">
              <div className="paragrafo-box">
                <p className="primeiro">
                  No matter what you shop online for. If the item comes in a box,<br />
                a cat owner will know: fun is guaranteed. Curious like no other<br />
                creature, cats soon turn the cardboard box into total play. With<br />
                that in mind, WHISKAS presents Curiosity Boxes, a free gift to<br />
                stimulate indoor play.
              </p>
                <p className="segundo">
                  In a partnership with Petlove, Brazils number-one online pet<br />
                supplies shop, customers get a free Curiosity Box with a<br />
                purchase of 40 single pouches. Each box will turn into a<br />
                rocket, airplane or submarine. Tutors can follow on-box setup<br />
                instructions and readily start the cat play.
              </p>
              </div>
            </div>
          </div>
          <div className="imagens whiskas">
            <div className="video-responsive whiskas">
              <iframe className="video-responsive-item" src="https://player.vimeo.com/video/441851339" title="Whiskas Video" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen></iframe>
            </div>
            <img src={Img1} className="img1" alt="Whiskas 1" />
            <img src={Img2} className="img2" alt="Whiskas 2" />
            <img src={Img3} className="img3" alt="Whiskas 3" />
            <img src={Img4} className="img4" alt="Whiskas 4" />
            <img src={Img5} className="img5" alt="Whiskas 5" />
            <img src={Img6} className="img6" alt="Whiskas 6" />
            <img src={Img7} className="img7" alt="Whiskas 7" />
            <img src={Img8} className="img8" alt="Whiskas 8" />
            <img src={Img9} className="img9" alt="Whiskas 9" />
            <img src={Img10} className="img10" alt="Whiskas 10" />
            <img src={Img11} className="img11" alt="Whiskas 11" />
            <img src={Img12} className="img12" alt="Whiskas 12" />
            <img src={Img13} className="img13" alt="Whiskas 13" />
            <img src={Img14} className="img14" alt="Whiskas 14" />
          </div>
          <Footer controlesFooter={this.controlesFooter} />
        </div>
      </div>
    );
  }
}

export default Whiskas;
