import React from 'react';

import Menu from '../Menu/index';
import Profile from '../../assets/about-me/img/profile.jpg';

import './style.scss';

class About extends React.Component {

  tipoMenu = 'about';

  render() {
    return (
      <div className="about-me">
        <div className="about-me-container">
          <Menu tipoMenu={this.tipoMenu} />
          <div className="content">
            <div className="profile-image">
              <img src={Profile} className="image" alt="Fernando Fregate Foto" />
            </div>
            <div className="alignment">
              <div className="description">
                <p>Im a 21-year-old<br />
                brazilian art director<br />
                based in sao paulo.<br />
                im a guitar player and<br />
                a wine enthusiast too.<br />
                </p>
                <p>
                  ( ive  designed some<br />
                  wine labels that you<br />
                  can see on my home page).<br />
                </p>
                <p>
                  agencies that<br />
                  ive worked for:<br />
                </p>
                <p>
                  mullen lowe brasil<br />
                2016 - 2018 <br />
                </p>
                <p>
                  almapbbdo<br />
                2018 - 2020<br />
                </p>
                <p>
                  david sao paulo<br />
                2020 - currently<br />
                </p>
                <p>
                  contact with me
                </p>
                <p>
                  fernando.fregate@gmail.com<br />
                  +55 011 95394-1464<br />
                </p>
                <div className="links-social-networks">
                  <a href="https://www.linkedin.com/in/fernando-fregate" target="_blank" rel="noopener noreferrer" className="icon linked-in" title="LinkedIn"><span>LinkedIn</span></a>
                  <a href="https://www.instagram.com/_fregate" target="_blank" rel="noopener noreferrer" className="icon instagram" title="Instagram"><span>Instagram</span></a>
                  <a href="https://www.facebook.com/fernando.fregate" target="_blank" rel="noopener noreferrer" className="icon facebook" title="Facebook"><span>Facebook</span></a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
