import React from 'react';
import { Link } from 'react-router-dom';

import Home from '../../assets/menu/svg/menu-home.svg';
import AboutMe from '../../assets/menu/svg/menu-about-me.svg';
import FernandoFregate from '../../assets/menu/svg/menu-fernando-fregate.svg';


import './style.scss';

class Menu extends React.Component {
  
  tipoMenu;

  constructor(props) {
    super(props);
    this.tipoMenu = props.tipoMenu;
  }


  render() {

    if(this.tipoMenu === 'about'){
      return (
        <div>
          <div className="menu">
            <div className="menu-container">
              <Link to="/" className="link home-link" title="Home">
                <img src={Home} alt="Home"/>
              </Link>
              <Link to="/" className="link fernando-fregate-link" title="Fernando Fregate">
                <img src={FernandoFregate} alt="Fernando Fregate"/>
              </Link>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <div className="menu">
            <div className="menu-container">
              <Link to="/" className="link home-link" title="Home">
                <img src={Home} alt="Home"/>
              </Link>
              <Link to="/about-me" className="link about-me-link" title="About me">
                <img src={AboutMe} alt="About me"/>
              </Link>
              <Link to="/" className="link fernando-fregate-link" title="Fernando Fregate">
                <img src={FernandoFregate} alt="Fernando Fregate"/>
              </Link>
            </div>
          </div>
        </div>
      );
    }

  }
}

export default Menu;
