import React from 'react';

import '../style.scss';

import Menu from '../../Menu/index';
import Footer from '../Footer/index'

import Gif1 from '../../../assets/campanhas/img/boticario/g1boticario.gif';
import Gif2 from '../../../assets/campanhas/img/boticario/g2boticario.gif';
import Gif3 from '../../../assets/campanhas/img/boticario/g3boticario.gif';
import Img1 from '../../../assets/campanhas/img/boticario/t1boticario.jpg';
import Img2 from '../../../assets/campanhas/img/boticario/t2boticario.jpg';
import Img3 from '../../../assets/campanhas/img/boticario/t3boticario.jpg';
import Img4 from '../../../assets/campanhas/img/boticario/t4boticario.jpg';


class Boticario extends React.Component {

  componentDidMount() {
    window.scrollTo(0, 0)
  }

  tipoMenu = 'campanhas';

  controlesAtivos = 'todos';
  links = {
    prev: '/whiskas',
    next: '/havaianas'
  }

  controlesFooter = {
    controlesAtivos: this.controlesAtivos,
    links: this.links
  }

  render() {
    return (
      <div className="campanha">
        <div className="campanha-container">
          <Menu tipoMenu={this.tipoMenu} />
          <div className="descricao-box">
            <div className="descricao-titulo">
              <div className="titulo-box">
                <h1 className="titulo-campanha">Intense Carnaval</h1>
                <p className="cliente-agencia">Client: O Boticrio | Agency: AlmapBBDO</p>
                <p className="funcao">Role: Concept / Art Direction</p>
              </div>
            </div>
            <div className="descricao-texto">
              <div className="paragrafo-box">
                <p className="primeiro">
                  Boticario's new makeup collection for Brazilian Carnival<br />
                  is meant to follow the Samba rhythim. And for the launch<br />
                  of the new Intense products, we  have shown that it makes<br />
                  so much more than that.
              </p>
                <p className="segundo">
                  The lipstick has been turned into a whistle, the eyeliner<br />
                  pencil and the body glitter have been turned into percussion<br />
                  instruments, and the lip gloss well it shines under the<br />
                  Carnival lights.
              </p>
              </div>
            </div>
          </div>
          <div className="imagens">
            <div className="video-responsive">
              <iframe className="video-responsive-item" src="https://player.vimeo.com/video/387724983" title="Boticrio Primeiro Video" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen></iframe>
            </div>
            <img src={Gif1} className="gif-boticario-1" alt="Boticario 1" />
            <img src={Gif2} className="gif-boticario-2" alt="Boticario 2" />
            <img src={Gif3} className="gif-boticario-3" alt="Boticario 3" />
            <div className="video-responsive">
              <iframe className="video-responsive-item" src="https://player.vimeo.com/video/388011014" title="Boticrio Segundo Video" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen></iframe>
            </div>
            <div className="video-responsive">
              <iframe className="video-responsive-item" src="https://player.vimeo.com/video/388017973" title="Boticrio Terceiro Video" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen></iframe>
            </div>
            <div className="video-responsive">
              <iframe className="video-responsive-item" src="https://player.vimeo.com/video/388317411" title="Boticrio Quarto Video" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen></iframe>
            </div>
            <img src={Img1} className="img-boticario-1" alt="Boticario 4" />
            <img src={Img2} className="img-boticario-2" alt="Boticario 5" />
            <img src={Img3} className="img-boticario-3" alt="Boticario 6" />
            <img src={Img4} className="img-boticario-4" alt="Boticario 7" />
          </div>
          <Footer controlesFooter={this.controlesFooter} />
        </div>
      </div>
    );
  }
}

export default Boticario;
