import React from 'react';

import './style.scss';

import { Link } from 'react-router-dom';

class MenuFooter extends React.Component {
  render() {
    return (
      <div className="menu-footer">
        <div className="line line-one">
          <Link to="/whiskas" className="link whiskas-link" title="Whiskas">Whiskas - Curiosity Box</Link>
          <span className="pipe">|</span>
          <Link to="/boticario" className="link boticario-link" title="Boticario">Boticrio - Intense Carnaval</Link>
          <span className="pipe">|</span>
          <Link to="/havaianas" className="link havaianas-link" title="Havaianas">Havaianas - Dia das Bruxas</Link>
        </div>
        <div className="line line-two">
          <Link to="/doritos" className="link doritos-link" title="Doritos">Doritos - Trigonometria</Link>
          <span className="pipe">|</span>
          <Link to="/burger-king" className="link burger-king-link" title="Burger King">Burger King - Flame Hour</Link>
          <span className="pipe">|</span>
          <Link to="/estadao" className="link estadao-link" title="Estadao">Estado - Cannes Winners</Link>
          <span className="pipe">|</span>
          <Link to="/wine-labels" className="link wine-labels-link" title="Wine Labels">Wine Labels</Link>
        </div>
      </div>
    )
  }
}

export default MenuFooter;