import React from 'react';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';

import Home from './pages/Home/index';
import About from './pages/About/index';
import Whiskas from './pages/Campaigns/Whiskas/index';
import Boticario from './pages/Campaigns/Boticario/index';
import Havaianas from './pages/Campaigns/Havaianas/index';
import Doritos from './pages/Campaigns/Doritos/index';
import BurgerKing from './pages/Campaigns/BurgerKing/index';
import Estadao from './pages/Campaigns/Estadao/index';
import WineLabels from './pages/Campaigns/WineLabels/index';

class Routes extends React.Component {

  render() {
    return (
      <Router>
        <Switch>
          <Route path='/' exact component={Home} />
          <Route path='/about-me' component={About} />
          <Route path='/whiskas' component={Whiskas} />
          <Route path='/boticario' component={Boticario} />
          <Route path='/havaianas' component={Havaianas} />
          <Route path='/doritos' component={Doritos} />
          <Route path='/burger-king' component={BurgerKing} />
          <Route path='/estadao' component={Estadao} />
          <Route path='/wine-labels' component={WineLabels} />
        </Switch>
      </Router>
    )
  }
}

export default Routes;