import React from 'react';

import './style.scss';

import Controls from './Controls/index';
import MenuFooter from './MenuFooter/index';

class Footer extends React.Component {

  controlesFooter;

  constructor(props) {
    super(props);
    this.controlesFooter = props.controlesFooter;
  }
  render() {
    return (
      <div>
        <div className="footer">
          <Controls controlesFooter={this.controlesFooter}/>
          <MenuFooter />
        </div>
      </div>
    );
  }
}

export default Footer;