import React from 'react';

import '../style.scss';

import Menu from '../../Menu/index';
import Footer from '../Footer/index'

import Img1 from '../../../assets/campanhas/img/burgerking/t1burgerking.jpg';
import Gif1 from '../../../assets/campanhas/img/burgerking/g1burgerking.gif';

class BurgerKing extends React.Component {

  componentDidMount() {
    window.scrollTo(0, 0)
  }

  tipoMenu = 'campanhas';

  controlesAtivos = 'todos';
  links = {
    prev: '/doritos',
    next: '/estadao'
  }

  controlesFooter = {
    controlesAtivos: this.controlesAtivos,
    links: this.links
  }

  render() {
    return (
      <div className="campanha">
        <div className="campanha-container">
          <Menu tipoMenu={this.tipoMenu} />
          <div className="descricao-box">
            <div className="descricao-titulo">
              <div className="titulo-box">
                <h1 className="titulo-campanha">The Flame Hour</h1>
                <p className="cliente-agencia">Client: Burger King | Agency: Mullen Lowe</p>
                <p className="funcao">Role: Concept / Art Direction</p>
              </div>
            </div>
            <div className="descricao-texto">
              <div className="paragrafo-box">
                <p className="primeiro">
                  The Earth Hour, is a worldwide movement organized by WWF<br />
                  to encourage individuals communities and businesses to turn<br />
                  off electric lights, for one hour, as a symbol of commitment to<br />
                  the planet. And Burger King obviously has joined this movement<br />
                  too, turning off all their lights.
                </p>
                <p className="segundo">
                  All the restaurants that have joined this movement shut their doors<br />
                  for one hour, but not Burger King, after all nothing more is necessary<br />
                  than just the fire to make a true Flame-Grilled-Whopper.
                </p>
                <p className="terceiro">
                  During the Earth Hour, the flames from the grills were the only<br />
                  lights used in the BK restaurants, and all the money raised from<br />
                  Whopper sales has been donated to WWF.
                </p>
              </div>
            </div>
          </div>
          <div className="imagens">
            <img src={Gif1} className="img1" alt="Burger King 1" />
            <img src={Img1} className="img2" alt="Burger King 1" />
          </div>
          <Footer controlesFooter={this.controlesFooter} />
        </div>
      </div>
    );
  }
}

export default BurgerKing;
