import React from 'react';

import '../style.scss';

import Menu from '../../Menu/index';
import Footer from '../Footer/index'

import Img1 from '../../../assets/campanhas/img/winelabels/t1wine.jpg';
import Img2 from '../../../assets/campanhas/img/winelabels/t2wine.jpg';
import Img3 from '../../../assets/campanhas/img/winelabels/t3wine.jpg';
import Img4 from '../../../assets/campanhas/img/winelabels/t4wine.jpg';

class WineLabels extends React.Component {

  componentDidMount() {
    window.scrollTo(0, 0)
  }

  tipoMenu = 'campanhas';

  controlesAtivos = 'prev';
  links = {
    prev: '/estadao',
    next: ''
  }

  controlesFooter = {
    controlesAtivos: this.controlesAtivos,
    links: this.links
  }

  render() {
    return (
      <div className="campanha">
        <div className="campanha-container">
          <Menu tipoMenu={this.tipoMenu} />
          <div className="descricao-box">
            <div className="descricao-titulo">
              <div className="titulo-box">
                <h1 className="titulo-campanha">Wine Labels</h1>
                <p className="cliente-agencia">Personal Project | Role: Concept / </p>
                <p className="funcao">Art Direction / Design / Illustration</p>
              </div>
            </div>
            <div className="descricao-texto">
              <div className="paragrafo-box">
                <p className="primeiro">
                  In 2019, I decided to start studying about something new. But it needed<br />
                  to be something totally different from everything I had ever studied<br />
                  in my lifetime. So, what about wines?
              </p>
                <p className="segundo">
                  After drinking a lot of wines and falling in love with the wine world,<br />
                  I've figured out that I didn't want just to learn about it, I wanted to be<br />
                  part of it. Beside the taste thing, for all the wines that I have ever tasted,<br />
                  I have looked at their labels, and felt that wines actually start outside<br />
                  the bottles, through the sensation that comes from the label. Guess what?<br />
                  Ive turned two loves into one personal project: Wine and Design.<br />
                  And started to concept wine label artworks.
              </p>
              </div>
            </div>
          </div>
          <div className="imagens">
            <img src={Img1} className="img-wine-1" alt="Imagem" />
            <img src={Img2} className="img-wine-2" alt="Imagem" />
            <img src={Img3} className="img-wine-3" alt="Imagem" />
            <img src={Img4} className="img-wine-4" alt="Imagem" />
          </div>
          <Footer controlesFooter={this.controlesFooter} />
        </div>
      </div>
    );
  }
}

export default WineLabels;
