import React from 'react';

import '../style.scss';

import Menu from '../../Menu/index';
import Footer from '../Footer/index'

import Img1 from '../../../assets/campanhas/img/havaianas/t1havaianas.jpg';
import Img2 from '../../../assets/campanhas/img/havaianas/t2havaianas.jpg';
import Img3 from '../../../assets/campanhas/img/havaianas/t3havaianas.jpg';
import Img4 from '../../../assets/campanhas/img/havaianas/t4havaianas.jpg';

class Havaianas extends React.Component {

  componentDidMount() {
    window.scrollTo(0, 0)
  }

  tipoMenu = 'campanhas';

  controlesAtivos = 'todos';
  links = {
    prev: '/boticario',
    next: '/doritos'
  }

  controlesFooter = {
    controlesAtivos: this.controlesAtivos,
    links: this.links
  }

  render() {
    return (
      <div className="campanha">
        <div className="campanha-container">
          <Menu tipoMenu={this.tipoMenu} />
          <div className="descricao-box">
            <div className="descricao-titulo">
              <div className="titulo-box">
                <h1 className="titulo-campanha">Dia das Bruxas</h1>
                <p className="cliente-agencia">Client: Havaianas | Agency: AlmapBBDO</p>
                <p className="funcao">Role: Concept / Art Direction</p>
              </div>
            </div>
            <div className="descricao-texto">
              <div className="paragrafo-box">
                <p className="primeiro">
                  Brazilian's folklore stories are full of realy scary creatures,<br />
                  like a horse without head, a giant snake on fire, a one-legged<br />
                  mystical man and a man with feet turned back around that<br />
                  kills jungle invasors.
              </p>
                <p className="segundo">
                  But despite of that, Brazilian people still celebrating the<br />
                  American halloween A.K.A: "Dia das Bruxas" in Brazil, with<br />
                  the same ghosts, monsters and werewolfs.
              </p>
                <p className="terceiro">
                  So, Havaianas, the most Brazilian flip-flops brand, decided<br />
                  to create a new collection with Brazilian folklores characters,<br />
                  to promote the first halloween with Brazilian monsters ever,<br />
                  or better saying, the first truly Brazilian "Dia das Bruxas".
              </p>
              </div>
            </div>
          </div>
          <div className="imagens">
            <img src={Img1} className="img1" alt="Havaianas 1" />
            <img src={Img2} className="img2" alt="Havaianas 2" />
            <img src={Img3} className="img3" alt="Havaianas 3" />
            <img src={Img4} className="img4" alt="Havaianas 4" />
          </div>
          <Footer controlesFooter={this.controlesFooter} />
        </div>
      </div>
    );
  }
}

export default Havaianas;
